<template>
  <div class="upload">
      <yd-layout>
    <h1 class="bigtitle">Upload your room</h1>

    <yd-cell-group title="">
        <yd-cell-item arrow>
            <span slot="left">District: &nbsp; </span>
                <select slot="right" v-model="district">
                    <option value="xuhui">xuhui</option>
                    <option value="jingan">jingan</option>
                    <option value="pudong">pudong</option>
                </select>
        </yd-cell-item>
    </yd-cell-group>

    <yd-cell-group title="Your house pictures">    
            <Vqiniu></Vqiniu>
    </yd-cell-group>

    <yd-cell-group title="How many rooms">
        
        <div class="room_desc_item">
            <span class="item-title">Rooms numbers : </span>
            <yd-spinner v-model="rooms_num" width="150px" height="40px" min="1" unit="1" max="5"></yd-spinner>
        </div>

        
            
    </yd-cell-group>

    <yd-cell-group>
        <yd-cell-item>
            <span slot="left"> Price: ¥ &nbsp;</span>
            <input slot="right" type="number" placeholder="" v-model="price">
        </yd-cell-item>
    </yd-cell-group>
    
   


   <yd-cell-group title="房屋设施"> 
            <yd-cell-item arrow @click.native="show4 = true">
               <span slot="left">Choose what you have{{checklist2}}</span>
            </yd-cell-item>
    </yd-cell-group>

     <yd-popup v-model="show4" position="bottom" height="60%">
               <yd-checklist align="right" v-model="checklist2">
        <yd-checklist-item val="Bath">
            <div style="height: 50px;line-height: 50px;">Bath</div>
        </yd-checklist-item>
        <yd-checklist-item val="Subway">
            <div style="height: 50px;line-height: 50px;">Subway</div>
        </yd-checklist-item>
        <yd-checklist-item val="3">
            <div style="height: 50px;line-height: 50px;">cool shit</div>
        </yd-checklist-item>
    </yd-checklist>
    <yd-button type="warning" style="margin: 30px;" @click.native="show4 = false">Close Bottom Popup</yd-button>
     </yd-popup>
    
    <yd-cell-group title="Describe your awesome room.. ">
        <yd-cell-item>
            <yd-textarea v-model="desc" slot="right" placeholder="Tell me your bank password" maxlength="100"></yd-textarea>
        </yd-cell-item>
    </yd-cell-group>
      


    <yd-cell-group title="My tips">
            <yd-cell-item type="label">
                <div slot="left">Welcome pet? </div>
                <yd-switch slot="right" v-model="pet"></yd-switch>
            </yd-cell-item>
             <yd-cell-item type="label">
                <div slot="left">I dont like guys? </div>
                <yd-switch slot="right" v-model="guys"></yd-switch>
            </yd-cell-item>
    </yd-cell-group>

    <yd-cell-group title="">
        <yd-cell-item arrow>
            <span slot="left">Gender : </span>
                <select slot="right">
                    <option value="1">guy</option>
                    <option value="2">girl</option>
                    <option value="3">other</option>
                </select>
        </yd-cell-item>
    </yd-cell-group>

    <yd-button-group>
        <yd-button size='large' type="primary" @click.native="UploadMyApartment">Upload</yd-button>
    </yd-button-group>



        </yd-layout>
  </div>
</template>
<script>
// import District from './address';
import { APIuploadroom } from '../../api/api'
import Vqiniu from '@/components/vqiniu'
import {Radio, RadioGroup,TextArea, CellItem, CellGroup,CitySelect,CheckList,CheckListItem} from 'vue-ydui'
export default {
    components:{
        Radio, 
        RadioGroup,
        CellGroup,
        CellItem,
        Vqiniu,
        CitySelect,
        CheckListItem,
        CheckList,
        TextArea,
        
    },
    data () {
        return {
            rooms_num:0, // 这里默认是0 注意
            district:'',
            files:[],
            price:'',

            pet:'',
            guys: '',
            checklist2:[],
            desc:'',
            show4:false
          

        }
    },
    methods: {
       UploadMyApartment() {
           APIuploadroom({
               'rooms':this.rooms_num,
               'price':this.price,
               'district':this.district
           }).then( res => {
               console.log(res.data)
           }).catch( err => {
               console.log('出错了！' + err)
           })
       }
    }
  
}
</script>




